import React, {Component} from 'react'
import ReactDOM from 'react-dom'

// 引入组件
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom'
// Provider是react-redux两个核心工具之一，作用：将store传递到每个项目中的组件中
// 第二个工具是connect，稍后会作介绍
import { Provider } from 'react-redux'
// 引入创建好的store实例
import store from './store/index'

import {asyncComponent} from './components/Test'

const Test=()=>asyncComponent(() => import("./components/Test"));


const App=()=> {

    return (
        <div>
            <h1>App</h1>
            <ul>
                <li><Link to="/about">About</Link></li>
                <li><Link to="/inbox">Inbox</Link></li>
                <li><Link to="/test">home</Link></li>
            </ul>
        </div>
    )

}

const About = ()=>{
    return(
     <h3>About</h3>
    )
}

const Inbox = ()=>{
        return (
            <div>
                <h2>Inbox</h2>
            </div>
        )
}

const Message =()=>{
        return <h3>Message</h3>

}

// 渲染DOM
ReactDOM.render (
    (
       < div>
            {/* 将store作为prop传入，即可使应用中的所有组件使用store */}
            <Provider store = {store}>
                <Router>
                    <div>
                        <Route path="/" component={App}/>
                        <Route path="/about" component={About} />
                        <Route path="/test " exact compoent={Test} />
                    </div>

                </Router>

            </Provider>
       </div>

    ),
    document.getElementById('root')
)


